import { Component, OnInit, TemplateRef } from '@angular/core';
import { NgxSpinnerService } from "ngx-spinner";
import { HttpClient, HttpEventType } from '@angular/common/http';
import { ApiService } from '../../api.service';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { DomSanitizer, SafeResourceUrl, SafeUrl} from '@angular/platform-browser';

@Component({
  selector: 'app-radio-cityguider',
  templateUrl: './radio-cityguider.component.html',
  styleUrls: ['./radio-cityguider.component.css']
})
export class RadioCityguiderComponent implements OnInit {
banner: File[] = [];
	files: File[] = [];
	itemBanner: File = null;
	itemFiles: File = null;
	cg_msg: any = '';
	cg_src: any = '';
	cg_img: any = '';
	cg_img_data: any = '';
	isSuccessAlert: any = false;
	isDangerAlert: any = false;
	announcer: any = [];
	messages: any = [];
	modalRef: BsModalRef;
	deleteItem: any = {};
	msg: any = '';

  constructor(
  	public spinner: NgxSpinnerService,
  	public http: HttpClient,
  	public api: ApiService,
  	public modalService: BsModalService,
  	private sanitizer: DomSanitizer
  	) { }

  ngOnInit(): void {

  	this.getData();

  }

  convertBlob(){
  	 // var reader = new FileReader();
  	 let url = this.sanitizer.bypassSecurityTrustResourceUrl
                            (this.cg_img_data);
	 // reader.readAsDataURL(url);
	 // var data; 
	 // reader.onloadend = function() {
	 //     var base64data = reader.result;                
	 //     console.log(base64data);
	 //     data = base64data;
	 // }
	 this.cg_img = url;
  }
  getData(){
  	this.api.provideData({
  		type: 'getcityguideradm'
  	}, 'data-adm-api').subscribe((resp)=>{
  		this.messages = resp;
  		console.log(this.messages);
  		
  	});
  }

  onSelect(event, type) {
		if(type=='files'){
			// console.log(event);
	  		this.files.push(...event.addedFiles);
	  		console.log(this.files);
	  		this.itemFiles = this.files[0];
		}
		else if(type=='banner'){
			console.log(event);
	  		this.banner.push(...event.addedFiles);
	  		this.itemBanner = this.banner[0];
	  		this.cg_img = '';
	  		 // const formData = new FormData();
    
	     //    for (var i = 0; i < this.banner.length; i++) { 
	     //      formData.append("banner[]", this.banner[i]);
	     //    }
	     //    formData.append("param", "banner");
	   
	     //    this.http.post(this.api.uploadUrl+'upload.php', formData)
	     //    .subscribe(res => {
	     //       console.log(res);
	     //       alert('Uploaded Successfully.');
	     //    })
		}
	  
	}
	 
	onRemove(event, type) {
		if(type=='files'){
			console.log(event);
	  		this.files.splice(this.files.indexOf(event), 1);
		}
		else if(type=='banner'){
			console.log(event);
	  		this.banner.splice(this.banner.indexOf(event), 1);
		}
	  
	}

	saveProgram(){
		this.spinner.show();
		
	  	const fd = new FormData();
	  	fd.append('param', 'create');
	  	fd.append('cg_msg', this.cg_msg);
	  	fd.append('cg_src', this.cg_src);
	  	fd.append('cg_img', this.cg_img);
	  	fd.append('banner[]', this.itemBanner);

	    this.http.post(this.api.uploadUrl+'upload-cg.php', fd)
		        .subscribe(res => {
		        this.spinner.hide();
		           console.log(res);
		           	this.isSuccessAlert = true;
		           	this.cg_msg = '';
					this.cg_src = '';
					this.banner = [];
					this.itemBanner = null;
					this.getData();
					setTimeout(()=>{ 
					    this.isSuccessAlert = false;
					 }, 3000);
		           // alert('Uploaded Successfully.');
		        });
	}

	deleteProgram(item){
  	this.api.provideData({
  		type: 'deletecityguideradm',
  		cg_id: item.cg_id,
  		cg_img_ori: item.cg_img_ori
  	}, 'data-adm-api').subscribe((resp)=>{
  		console.log(resp);
  		this.getData();
  	});
  }

	openModal(template: TemplateRef<any>, item) {
    this.modalRef = this.modalService.show(template, {class: 'modal-sm'});
    this.deleteItem = item;
  }

  confirm(): void {
    this.msg = 'Confirmed!';
    this.deleteProgram(this.deleteItem);
    this.modalRef.hide();
  }
 
  decline(): void {
    this.msg = 'Declined!';
    this.modalRef.hide();
  }

}
